import React from 'react';

const LandingPage = () => {
  return (
    <div style={{ fontFamily: 'Arial, sans-serif', textAlign: 'center', padding: '20px', backgroundColor: '#f9f9f9' }}>
      <header style={{ marginBottom: '40px' }}>
        <h1 style={{ fontSize: '3em', color: '#333' }}>Welcome to Pretty Petts</h1>
        <p style={{ fontSize: '1.2em', color: '#666' }}>The NFT Marketplace for Pet Lovers</p>
      </header>

      <main>
        <section style={{ marginBottom: '40px' }}>
          <h2 style={{ fontSize: '2em', color: '#444' }}>Join Our Waitlist</h2>
          <p style={{ fontSize: '1em', color: '#555' }}>Be the first to know when we launch!</p>
          <form style={{ marginTop: '20px' }}>
            <input
              type="email"
              placeholder="Enter your email"
              style={{ padding: '10px', fontSize: '1em', width: '300px', borderRadius: '5px', border: '1px solid #ccc' }}
            />
            <button
              type="submit"
              style={{ padding: '10px 20px', fontSize: '1em', marginLeft: '10px', borderRadius: '5px', border: 'none', backgroundColor: '#007BFF', color: '#fff', cursor: 'pointer' }}
            >
              Join Now
            </button>
          </form>
        </section>

        <section>
          <h2 style={{ fontSize: '2em', color: '#444' }}>Why Invest in Pretty Petts?</h2>
          <p style={{ fontSize: '1em', color: '#555', maxWidth: '600px', margin: '0 auto' }}>
            Pretty Petts is revolutionizing the NFT space by creating a dedicated marketplace for pet enthusiasts. With a focus on community, creativity, and innovation, we aim to connect pet lovers and investors in a unique and meaningful way.
          </p>
        </section>
      </main>

      <footer style={{ marginTop: '40px', color: '#888' }}>
        <p>&copy; 2023 Pretty Petts. All rights reserved.</p>
      </footer>
    </div>
  );
};

export default LandingPage;